.container {
	max-width: 1100px;
	margin: auto;
	overflow: hidden;
	padding: 0 2rem;
	margin-top: 6rem;
	margin-bottom: 3rem
}

.x-large {
	font-size: 4rem
}

.text-primary {
	color: #17a2b8
}

.large {
	font-size: 3rem;
	line-height: 1.2;
	margin-bottom: 1rem
}

.lead {
	font-size: 1.5rem;
	margin-bottom: 1rem
}

.line {
	height: 1px;
	background: #ccc;
	margin: 1.5rem 0
}

.bg-primary {
	background-color: #17a2b8;
	color: #fff
}

.bg-light {
	background-color: #f4f4f4;
	color: #333
}

.bg-dark {
	background-color: #343a40;
	color: #fff
}

.bg-success {
	background-color: #28a745;
	color: #fff
}

.bg-danger {
	background-color: #dc3545;
	color: #fff
}

.bg-white {
	background-color: #fff;
	color: #333;
	border: #ccc 1px solid
}

.btn {
	display: inline-block;
	background: #f4f4f4;
	color: #333;
	padding: 0.4rem 1.3rem;
	border: none;
	cursor: pointer;
	font-size: 1rem;
	margin-right: 0.5rem;
	outline: none;
	transition: all 0.2s ease-in
}

.btn.btn-primary {
	background-color: #17a2b8;
	color: #fff
}

.btn.btn-primary:hover {
	background: #1ab6cf
}

.btn.btn-primary {
	background-color: #17a2b8;
	color: #fff
}

.btn.btn-primary:hover {
	background: #1ab6cf
}

.btn.btn-dark {
	background-color: #343a40;
	color: #fff
}

.btn.btn-dark:hover {
	background: #3f474e
}

.btn.btn-success {
	background-color: #28a745;
	color: #fff
}

.btn.btn-success:hover {
	background: #2dbc4e
}

.btn.btn-danger {
	background-color: #dc3545;
	color: #fff
}

.btn.btn-danger:hover {
	background: #e04b59
}

.btn:hover {
	background: #626d78;
	color: #fff
}

.alert {
	padding: 0.8rem;
	margin: 1rem;
	opacity: 0.9;
	background: #f4f4f4;
	color: #333
}

.alert.alert-primary {
	background-color: #17a2b8;
	color: #fff
}

.alert.alert-dark {
	background-color: #343a40;
	color: #fff
}

.alert.alert-success {
	background-color: #28a745;
	color: #fff
}

.alert.alert-danger {
	background-color: #dc3545;
	color: #fff
}

.badge {
	font-size: 0.8rem;
	padding: 0.1rem;
	text-align: center;
	margin: 0.3rem;
	background: #f4f4f4;
	color: #333
}

.badge.badge-primary {
	background-color: #17a2b8;
	color: #fff
}

.badge.badge-dark {
	background-color: #343a40;
	color: #fff
}

.badge.badge-success {
	background-color: #28a745;
	color: #fff
}

.badge.badge-danger {
	background-color: #dc3545;
	color: #fff
}

.dark-overlay {
	height: 100%;
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.7)
}

.table th,
.table td {
	padding: 1rem;
	text-align: left
}

.table th {
	background-color: #f4f4f4
}

.form-group {
	margin: 1.2rem 0
}

.form-text {
	display: block;
	margin-top: 0.3rem;
	color: #888
}

.form input[type='text'],
.form input[type='email'],
.form input[type='password'],
.form input[type='date'],
.form select,
.form textarea {
	display: block;
	width: 100%;
	padding: 0.4rem;
	font-size: 1.2rem;
	border: 1px solid #ccc
}

.form input[type='submit'] {
	font: inherit
}

.form .social-input {
	display: flex
}

.form .social-input i {
	padding: 0.5rem;
	width: 4rem
}

.form .social-input i.fa-twitter {
	color: #38a1f3
}

.form .social-input i.fa-facebook {
	color: #3b5998
}

.form .social-input i.fa-instagram {
	color: #3f729b
}

.form .social-input i.fa-youtube {
	color: #c4302b
}

.form .social-input i.fa-linkedin {
	color: #0077b5
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0
}

body {
	font-family: 'Raleway', sans-serif;
	font-size: 1rem;
	line-height: 1.6;
	background-color: #fff;
	color: #333
}

a {
	text-decoration: none;
	color: #17a2b8
}

img {
	width: 100%
}

ul {
	padding: 0
}

ul li {
	list-style-type: none;
	margin: 0
}

.navbar {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0.7rem 2rem;
	position: fixed;
	z-index: 1;
	width: 100%;
	top: 0;
	border-bottom: solid 1px #17a2b8;
	opacity: 0.9
}

.navbar ul {
	display: flex
}

.navbar li {
	list-style-type: none
}

.navbar a {
	color: #fff;
	padding: 0.45rem;
	margin: 0 0.25rem
}

.navbar a:hover {
	color: #17a2b8
}

.landing {
	position: relative;
	background: url(/img/showcase.jpg) no-repeat center center/cover;
	height: 100vh
}

.landing-inner {
	color: #fff;
	height: 100%;
	display: flex;
	flex-direction: column;
	width: 80%;
	margin: auto;
	align-items: center;
	justify-content: center;
	text-align: center
}

.profile {
	display: grid;
	grid-template-columns: 2fr 4fr 2fr;
	align-items: center;
	grid-gap: 2rem;
	padding: 1rem;
	line-height: 1.8;
	margin-bottom: 1rem
}

.round-img {
	border-radius: 50%
}

.profile-grid {
	display: grid;
	grid-template-areas: 'top top' 'about about' 'exp edu' 'github github';
	grid-gap: 1rem
}

.profile-grid .profile-top {
	grid-area: top;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center
}

.profile-grid .profile-top img {
	width: 250px
}

.profile-grid .profile-top .icons a {
	color: #fff;
	margin: 0 0.3rem
}

.profile-grid .profile-top .icons a:hover {
	color: #343a40
}

.profile-grid .profile-about {
	grid-area: about;
	text-align: center
}

.profile-grid .profile-about .skills {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center
}

.profile-grid .profile-exp {
	grid-area: exp
}

.profile-grid .profile-edu {
	grid-area: edu
}

.profile-grid .profile-exp>div,
.profile-grid .profile-edu>div {
	margin-bottom: 1rem;
	padding-bottom: 1rem;
	border-bottom: #ccc;
	border-bottom: #ccc 1px dotted
}

.profile-grid .profile-exp:last-child,
.profile-grid .profile-edu:last-child {
	border: none
}

.profile-grid .profile-exp p,
.profile-grid .profile-edu p {
	margin: 0.5rem 0
}

.profile-grid .profile-github {
	grid-area: github
}

.profile-grid .profile-github .repo {
	display: flex
}

.profile-grid .profile-github .repo>div:first-child {
	flex: 7;
	flex-basis: 70%
}

.profile-grid .profile-github .repo>div:last-child {
	flex: 3;
	flex-basis: 20%
}

.post-form-header {
	padding: 0.5rem
}

.post {
	display: grid;
	grid-template-columns: 1fr 4fr;
	grid-gap: 2rem;
	align-items: center
}

.post>a:first-child {
	text-align: center
}

.post img {
	width: 150px
}

@media screen and (max-width: 700px) {
	.hide-sm {
		display: none
	}
	.x-large {
		font-size: 3rem
	}
	.large {
		font-size: 2rem
	}
	.lead {
		font-size: 1rem
	}
	.navbar {
		display: block;
		text-align: center;
		margin-bottom: 20px
	}
	.navbar ul {
		text-align: center;
		justify-content: center
	}
	.navbar h1 {
		margin-bottom: 1rem
	}
	.dash-buttons a {
		display: block;
		width: 100%;
		margin-bottom: 0.2rem
	}
	.profile {
		grid-template-columns: 1fr;
		text-align: center;
	}
	.profile ul {
		display: none
	}
	.profile-grid {
		grid-template-areas: 'top' 'about' 'exp' 'edu' 'github'
	}
	.profile-about .skills {
		flex-direction: column
	}
	.post {
		grid-template-columns: 1fr
	}
	.post a,
	.post button {
		padding: 0.3rem 0.4rem
	}
}